.JobCards_Wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
}

.NoTracking {
    width: 50%;
    margin: 0 auto;
    margin-top: 30%;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    letter-spacing: 0.045em;
    color: #A3A3A3;
  }

  @media (min-width: 768px) and (max-width: 1024px) {
    .JobCards_Wrap {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        align-items: center;
        height: 100vh;
    }
  }